import React, { useState, useEffect, useContext,useRef } from "react";
import Grid from "@mui/material/Grid";
import { useSelector } from "react-redux";
import Axios from "axios";
import { useHistory, useParams } from "react-router-dom";
import UserContext from "../../context/UserContext";
import "./index.css";
// import Check from './check.gif'
import Moment from "react-moment";
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
 import { useReactToPrint } from "react-to-print";
 import Pdf from '@mikecousins/react-pdf';



export default function Confirm() {
    let { id } = useParams();
    const history = useHistory();
    const { loginStatus, pdf } = useContext(UserContext);
    const [userInfo, setUserInfo] = useState({
        name: "",
        doc: "",
    });
    let customerId = useSelector((state) => state.email.customerId);
    if(customerId === null){
        history.push("/");
    }
    useEffect(() => {
        if (!loginStatus) {
            history.push("/");
            return;
        }

        // Get customer information from "customer" table.
        Axios.get("/customers/get/" + customerId).then(async (response) => {

            setUserInfo({
                ...userInfo,
                "name": response.data[0].name,
                "doc": response.data[0].doc,
            });
        });
        return()=>{
            setUserInfo({});
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const transList = () => {
        history.push("/translist");
    };
    const currentDate = new Date();
    const curFormatDate = currentDate;
    const print = ()=>{
        handlePrint();
    }
    const page= 1;
    const componentRef = useRef();
    const handlePrint = useReactToPrint({
        content: () => componentRef.current,
       });
    const share = async () => {
        const response = await fetch(pdf);
  // here image is url/location of image
  const blob = await response.blob();
  const file = new File([blob], `${userInfo["name"]? userInfo["name"] : 'Invited'}.pdf`, {type: blob.type});
  if(navigator.share) {
    await navigator.share({
      title: "Share",
      text: "Hello sir.",
      url: window.location.href,
      files: [file]
    })
      .then(() => console.log('Successful share'))
      .catch((error) => console.log('Error in sharing', error));
  }else {
    console.log(`system does not support sharing files.`);
  }
    }
    useEffect(()=> {
        if (navigator.share === undefined) {
          if (window.location.protocol === 'http:') {
            window.location.replace(window.location.href.replace(/^http:/, 'https:'));
          } 
        }
      }, []);
    return (
        <div className="confirm">
            <Grid
                container
                justifyContent="center"
                alignItems="center"
                direction="column"
            >
               <img src="https://elenacharameli.com/wp-content/uploads/2019/02/check-mark-animated-gif-12.gif" alt="checkImage" className="check" />
                <p className="username">
                    
                    {userInfo["name"] ? userInfo["name"] : <>Invited</>}
                </p>
                <p className="p-amount"><sup><sub>S/</sub></sup>{id}</p>
                <Moment format="Do MMMM YYYY - HH:mm">
                                    {curFormatDate}
                        </Moment>
               
            </Grid>
            <Grid
                container
                justifyContent="space-between"
                alignItems="center"
                direction="row"
                sx={{ padding: "50px 40px" }}
            >
                <IconButton aria-label="home" onClick={transList}>
                    <i  className="fas fa-home"></i >
                </IconButton>
                <IconButton aria-label="share" onClick={share}>
                    <i className="fas fa-share-alt"></i>
                </IconButton>
                <IconButton aria-label="print" onClick={print}>
                    <i className="fas fa-print"></i>
                    <div style={{ display: "none" }}>
                        <div ref={componentRef} style={{padding:'0px', margin:'0px'}}>
                            <Pdf file={pdf.toString()} page={page}/>
                        </div>
                    </div>
                </IconButton>
                
                
            </Grid>
            <Button onClick={()=> history.push('/customer')} sx={{mt: 4}} variant="contained" >Send Other</Button>
        </div>
    );
}
